.btns {
  /* display: inline-block; */
  /* font-weight: 400; */
  text-align: center;
  /* white-space: nowrap; */
  /* vertical-align: middle; */
  /* -webkit-user-select: none; */
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
